.application {
  padding: 60px 0;
  border-bottom: 1px solid #E7B656;
  margin-bottom: 30px;
}
.application__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 80px;
}
.application__nav-list {
  display: flex;
  list-style: none;
  padding: 0;
  gap: 30px;
}
.application__nav-item {
  flex-shrink: 0;
}
.application__nav-item a {
  color: #fff;
  font-size: 24px;
  line-height: 32px;
  border-bottom: 1px solid transparent;
  transition: color 0.3s, border-color 0.3s;
}
.application__nav-item a:hover {
  color: #E7B656;
  border-color: #E7B656;
}
.application__content {
  display: flex;
  gap: 57px;
}
.application__content-intro {
  display: flex;
  width: 35%;
  flex-direction: column;
}
.application__caption {
  font-size: 40px;
  line-height: 45px;
  font-weight: 500;
  color: #E7B656;
  margin-bottom: 30px;
}
.application__text {
  font-size: 24px;
  line-height: 32px;
  font-weight: 400;
  margin-bottom: 30px;
}

.form {
  font-size: 24px;
  line-height: 32px;
  font-weight: 400;
  width: 700px;
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.form__input {
  width: 100%;
  box-sizing: border-box;
  color: #fff;
  border: none;
  border-bottom: 1px solid rgba(219, 219, 219, 0.5215686275);
}
.form__checkbox {
  width: 32px;
  height: 32px;
}
.form__label {
  flex-shrink: 0;
}
.form__label--consent {
  font-size: 18px;
  line-height: 26px;
  display: flex;
  gap: 10px;
  align-items: center;
}
.form__colored {
  color: #E7B656;
}
.form select {
  width: 100%;
  box-sizing: border-box;
  background-color: transparent;
  color: rgba(219, 219, 219, 0.5215686275);
  border: none;
  border-bottom: 1px solid rgba(219, 219, 219, 0.5215686275);
}
.form__row {
  display: flex;
  gap: 10px;
}
.form__group {
  display: flex;
  flex-grow: 1;
  align-items: baseline;
  gap: 10px;
  flex-shrink: 0;
}

.social {
  display: flex;
  gap: 23px;
}
.social a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 55px;
  height: 55px;
  background-color: #003053;
  border-radius: 11px;
  transition: background-color 0.3s;
  cursor: pointer;
}
.social a:hover {
  background-color: #E7B656;
}