.contacts {
  padding: 60px 0 0 0;
  border-bottom: 1px solid #E7B656;
}
.contacts__wrapper {
  display: flex;
  align-items: center;
  gap: 60px;
}
.contacts__content {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.contacts__block {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.contacts__heading {
  font-size: 40px;
  line-height: 45px;
  color: #E7B656;
  font-weight: 400;
}
.contacts__caption {
  font-size: 28px;
  line-height: 33px;
  color: #E7B656;
  font-weight: 400;
}
.contacts__banner {
  position: relative;
  height: 400px;
  width: 100%;
}
.contacts__banner img {
  position: absolute;
  inset: 0;
  height: 100%;
  width: 100vw;
  object-fit: cover;
}
.contacts__link {
  font-size: 28px;
  line-height: 33px;
  color: #ffffff;
  transition: color 0.3s;
}
.contacts__link:hover {
  color: #e6b24d;
}