@charset "UTF-8";
.partners {
  padding: 100px 0;
}
.partners__text {
  display: flex;
  align-items: center;
  gap: 20px;
}
.partners__text span:first-child {
  display: flex;
  font-size: 325px;
  line-height: 296px;
  letter-spacing: -2px;
  background: linear-gradient(to bottom, #E7B656, #816630);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* Скролл картинок */
.scrolled {
  margin-top: -55px;
}

.scrolled__block {
  position: relative;
  overflow: hidden;
}

.scrolled__content {
  display: flex;
  width: 100%;
  gap: 25px;
  justify-content: center;
}

.scrolled__item {
  flex-shrink: 0;
  width: 362px;
  height: 168px;
  padding: 40px 45px;
  border: 1px solid #fff;
  background-color: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(10px);
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 0.3s;
  opacity: 0.5;
}
.scrolled__item.active {
  opacity: 1;
}
.scrolled__item img {
  object-fit: contain;
}

.scrolled__line {
  position: relative;
  display: flex;
  flex-direction: row;
  gap: 30px;
}

.scrolled__line-content {
  display: flex;
  gap: 25px;
  flex-direction: row;
  width: 100%;
  animation: scroll 120s infinite linear;
}

@keyframes scroll {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-100%);
  }
}