.slider {
  margin-top: 100px;
  display: flex;
  justify-content: space-between;
}
.slider--friends {
  padding-top: 100px;
  border-top: 1px solid #E7B656;
}
.slider__slide {
  position: absolute;
  inset: 0;
}
.slider__slide--text {
  position: relative;
}
.slider__slides {
  position: relative;
  height: 430px;
  width: 745px;
}
.slider__slides--text {
  height: auto;
}
.slider__nav {
  display: flex;
  align-items: center;
  gap: 10px;
}
.slider__nav-item {
  width: 20px;
  height: 20px;
  background-color: #434242;
  border-radius: 50%;
  cursor: pointer;
}
.slider__nav-item.active {
  background-color: #E7B656;
}
.slider__controls-wrapper {
  display: flex;
  width: 100%;
  height: 32px;
  justify-content: space-between;
  margin-top: auto;
}
.slider__controls-wrapper--friends {
  flex-direction: column-reverse;
  margin: 0;
  height: 100%;
  justify-content: space-between;
}
.slider__controls {
  display: flex;
  flex-grow: 1;
  margin-right: 105px;
}
.slider__arrow {
  height: 32px;
  width: 70px;
  cursor: pointer;
}
.slider__arrow--right {
  transform: scaleX(-1);
}
.slider__content {
  display: flex;
  flex-direction: column;
}
.slider__gold-text {
  font-size: 128px;
  line-height: 115px;
  color: #E7B656;
}