.header {
  background-color: transparent;
  position: relative;
  z-index: 1;
  height: 63px;
}
.header__wrapper {
  position: relative;
  padding-top: 53px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.header__burger {
  align-self: flex-start;
  display: block;
  width: 72px;
  height: 31px;
  flex-shrink: 0;
  background-color: transparent;
  color: #fff;
  padding: 0;
  border: none;
  position: relative;
  z-index: 3;
  cursor: pointer;
  transition: opacity 0.3s;
}
.header__burger i {
  display: block;
  height: 3px;
  width: 72px;
  border-radius: 2px;
  background: currentColor;
  left: 0;
  top: 0;
  margin-bottom: 11px;
}
.header__burger:hover {
  opacity: 0.7;
}
.header__langs {
  position: absolute;
  bottom: 20px;
  left: calc(50% - 57px);
}
.header__langs a {
  color: #fff;
  font-size: 28px;
  line-height: 33px;
  transition: color 0.3s;
}
.header__langs a.current {
  color: #E7B656;
}
.header__langs a:hover {
  color: #E7B656;
}
.header__contact-us {
  flex-shrink: 0;
  align-self: flex-start;
  color: #fff;
  font-size: 24px;
  line-height: 35px;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 10px 20px;
  border-radius: 15px 0 15px 15px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}
.header__contact-us--white {
  background-color: #ffffff;
  color: #333333;
}
.header__contact-us--brown {
  margin-top: auto;
  margin-left: auto;
  padding: 10px 50px;
  background-color: #5d5e62;
}
.header__contact-us:hover {
  background-color: #E7B656;
  color: #ffffff;
}
.header__contact-us--yellow {
  background-color: #E7B656;
  color: #ffffff;
}
.header__contact-us--yellow:hover {
  background-color: #ffffff;
  color: #333333;
}