.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background-color: #E7B656;
  color: #333333;
  width: 100%;
}

.footer__container {
  display: grid;
  grid-template-columns: 1fr repeat(1, 1fr) 1fr;
  gap: 20px;
  width: 100%;
}

.footer__column {
  text-align: center;
}

.footer__column--copyright {
  font-size: 20px;
  line-height: 29px;
  text-align: left;
}

.footer__column--privacy-policy {
  text-align: right;
}

.footer__column--privacy-policy,
.footer__column--design-studio {
  font-size: 20px;
  line-height: 29px;
}

.footer__column a {
  color: #333333;
  text-decoration: none;
  display: inline-block;
  flex-shrink: 0;
  border-bottom: 1px solid transparent;
  transition: border-color 0.3s;
}

.footer__column a:hover {
  border-color: #333333;
}