.services {
	padding: 100px 0;

	&__title {
		width: 100%;
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		margin-bottom: 50px;
		text-align: center;
		font-weight: 400;
		font-size: 325px;
		line-height: 292px;
		letter-spacing: -2px;
		background: linear-gradient(to bottom, #E7B656, #44371c);
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
		background-size: 100% 200%;
		animation: gradientAnimation 3s ease infinite;
	}

	&__subtitle {
		text-align: center;
		font-size: 40px;
		line-height: 45px;
		font-weight: 500;
	}

	&__list {
		display: flex;
		// flex-wrap: wrap;
		gap: 20px;
		padding: 50px 0;
	}

	&__card {
		position: relative;
		overflow: hidden;
		padding: 180px 50px 50px 50px;
		display: flex;
		flex-shrink: 0;
		flex-direction: column;
		height: 600px;
		width: 745px;
		border-radius: 0px 15px 15px 15px;

		&.is-active {
			.services__card-name {
				color: #E7B656;
			}
			
			.services__overlay {
				opacity: 1;
			}
		}

		&--small {
			width: 360px;
			border-radius: 15px;
		}
	}

	&__card-bg {
		position: absolute;
		inset: 0;
		z-index: -2;

		& img {
			position: absolute;
			inset: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__overlay {
		position: absolute;
		z-index: -1;
		width: 100%;
		height: 100%;
		background-color: #333333CC;
		inset: 0;
		opacity: 0;
		transition: opacity .3s;
	}

	&__card-name {
		position: relative;
		font-size: 64px;
		line-height: 72px;
		font-weight: 400;
		color: #ffffff;
		padding-bottom: 20px;

		&::before {
			// позже заменит на миксин во всех местах
			position: absolute;
			content: '';
			display: block;
			left: -50px;
			bottom: 0;
			width: 300px;
			height: 2px;
			background-color: #E7B656;
		}
	}

	&__card-description {
		font-size: 24px;
		line-height: 32px;
		color: #ffffff;
	}
}

@keyframes gradientAnimation {
	0% {
		background-size: 100% 100%;
	}
	50% {
		background-size: 200% 200%;
	}
	100% {
		background-size: 100% 100%;
	}
}