.categories {
  padding-bottom: 100px;
}

.tabs {
  margin-bottom: 30px;
}
.tabs__header {
  display: flex;
  position: relative;
  gap: 70px;
  margin: 30px 0;
  padding-left: 30px;
}
.tabs__header::before {
  position: absolute;
  display: block;
  content: "";
  top: 0;
  left: 0;
  width: 1px;
  height: 100%;
  background-color: #E7B656;
}
.tabs__caption {
  color: #E7B656;
}
.tabs__text {
  font-size: 24px;
  line-height: 32px;
}
.tabs__controls {
  display: flex;
  justify-content: center;
  border-top: 1px solid #ffffff;
  border-bottom: 1px solid #ffffff;
}
.tabs__select {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.tabs__select select {
  width: 365px;
  background-color: transparent;
  padding: 14px 40px 14px 20px;
  border-radius: 15px;
  border-color: #E7B656;
  color: #ffffff;
  font-size: 24px;
  line-height: 32px;
  cursor: pointer;
}
.tabs__select span {
  font-size: 24px;
  line-height: 32px;
  color: #E7B656;
}
.tabs__button {
  padding: 37px 0;
  width: 376px;
  border-left: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
  color: #DBDBDB;
  cursor: pointer;
  flex-shrink: 0;
}
.tabs__button.is-active {
  color: #E7B656;
}
.tabs__button:nth-child(1) {
  border-left: none;
}
.tabs__button:last-child {
  border-right: none;
}
.tabs__slider-head {
  display: flex;
  flex-direction: column;
}
.tabs__slider-head-caption {
  font-size: 24px;
  line-height: 32px;
  color: #E7B656;
  margin-bottom: 15px;
}
.tabs__slider-controls {
  display: flex;
  gap: 56px;
  padding-bottom: 30px;
}
.tabs__slider-controls-row {
  display: flex;
  gap: 20px;
}
.tabs__slider-controls-row button {
  color: #ffffff;
  opacity: 0.4;
}
.tabs__slider-controls-row button.active {
  color: #E7B656;
  opacity: 1;
}
.tabs__slider {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
}
.tabs__slide {
  position: relative;
  width: 1010px;
  height: 535px;
  padding: 40px 25px;
  border-radius: 14px;
  overflow: hidden;
  flex-shrink: 0;
  opacity: 0.3;
}
.tabs__slide img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.tabs__slide.is-active {
  width: 1130px;
  height: 598px;
  opacity: 1;
  border: 2px solid rgba(255, 255, 255, 0.2);
}
.tabs__slide.is-active .tabs__slide-gallery,
.tabs__slide.is-active .tabs__slide-info {
  opacity: 1;
  pointer-events: auto;
}
.tabs__slide-gallery {
  height: 90%;
  width: 100px;
  overflow: hidden;
  display: flex;
  gap: 10px;
  flex-direction: column;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s;
}
.tabs__slide-gallery-item {
  position: relative;
  width: 100%;
  height: 60px;
  border-radius: 3px;
  border: 1px solid #333333;
  overflow: hidden;
  flex-shrink: 0;
  opacity: 0.3;
  cursor: pointer;
}
.tabs__slide-gallery-item.is-active {
  opacity: 1;
}
.tabs__slide-info {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 45%;
  padding: 35px;
  background-color: rgba(67, 66, 66, 0.8);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s;
}
.tabs__slide-info-controls {
  display: flex;
  align-items: center;
  gap: 15px;
}
.tabs__slide-info-controls button:nth-child(2) {
  display: flex;
  gap: 10px;
  color: #E7B656;
  transition: opacity 0.3s;
}
.tabs__slide-info-controls button:nth-child(2):hover {
  opacity: 0.8;
}
.tabs__slide-info-category {
  color: #E7B656;
  font-size: 24px;
  line-height: 32px;
}
.tabs__slide-info-name {
  font-size: 40px;
  line-height: 45px;
  font-weight: 500;
}
.tabs__slide-info-description {
  font-size: 24px;
  line-height: 32px;
}