.world__title {
  width: 100%;
  margin-bottom: 50px;
  font-weight: 400;
  font-size: 128px;
  line-height: 115px;
  letter-spacing: -2px;
  background: linear-gradient(to bottom, #E7B656, #816630);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.world__subtitle {
  font-size: 40px;
  line-height: 45px;
  font-weight: 500;
  padding-top: 150px;
  margin-bottom: 150px;
}
.world__map {
  position: relative;
  height: 610px;
  width: 100vw;
  margin-bottom: 76px;
}
.world__map img {
  position: absolute;
  width: 100%;
  height: 100%;
  inset: 0;
  object-fit: cover;
}
.world__map-controls {
  position: relative;
  width: 100%;
  height: 610px;
  padding: 50px;
}
.world__map-tooltip {
  display: flex;
  align-items: center;
  font-size: 24px;
  line-height: 28px;
  width: 429px;
  padding: 30px;
  border-radius: 13px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  background-color: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
}
.world__map-tooltip button {
  pointer-events: none;
}
.world__map-toggle {
  flex-shrink: 0;
  position: relative;
  width: 26px;
  height: 26px;
  border-radius: 50%;
  border: 1px solid;
  border-color: #ffffff;
  background-color: #434242;
  box-shadow: 0px 0px 10px 0px rgba(255, 255, 255, 0.75);
  cursor: pointer;
  transition: border-color 0.3s;
}
.world__map-toggle .world__map-toggle-info {
  font-size: 24px;
  line-height: 32px;
  position: absolute;
  padding: 20px;
  top: calc(50% - 114px);
  left: calc(50% - 114px);
  width: 228px;
  height: 228px;
  border: 1px solid #E7B656;
  border-radius: 50%;
  background-color: rgba(231, 182, 86, 0.2);
  backdrop-filter: blur(2px);
  color: #ffffff;
  transform: scale(0);
  transition: transform 0.3s;
}
.world__map-toggle:before {
  display: block;
  position: absolute;
  content: "";
  z-index: 1;
  top: calc(50% - 8px);
  left: calc(50% - 8px);
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #ffffff;
  transition: background-color 0.3s;
}
.world__map-toggle--1 {
  position: absolute;
  left: 64%;
  top: 65%;
}
.world__map-toggle--2 {
  position: absolute;
  left: 80%;
  top: 76%;
}
.world__map-toggle--3 {
  position: absolute;
  left: 67%;
  top: 40%;
}
.world__map-toggle--4 {
  position: absolute;
  left: 100%;
  top: 40%;
}
.world__map-toggle--5 {
  position: absolute;
  left: 110%;
  top: 60%;
}
.world__map-toggle:hover {
  border: 1px solid #E7B656;
}
.world__map-toggle:hover .world__map-toggle-info {
  transform: scale(1);
}
.world__map-toggle:hover:before {
  background-color: #E7B656;
}
.world__map-arrow {
  margin-right: 15px;
}