.cooperation {
	padding: 0 0 100px 0;

	&__header {
		position: relative;
		padding-top: 80px;
	}

	&__title {
		width: 100%;
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		text-align: center;
		font-weight: 400;
		font-size: 325px;
		line-height: 292px;
		letter-spacing: -2px;
		background: linear-gradient(to left, #E7B656, #816630, #071723);
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
		background-size: 100% 200%;
		animation: gradientAnimation 3s ease infinite;
	}

	&__subtitle {
		position: absolute;
		top: 0;
		display: inline-block;
		font-size: 96px;
		line-height: 87px;
	}

	&__cards {
		display: flex;
		width: 100%;
		gap: 23px;
		margin-top: -100px;
		position: relative;
		z-index: 1;
	}

	&__caption {
		color: #E7B656;
		font-size: 40px;
		line-height: 45px;
	}

	&__text {
		font-size: 24px;
		line-height: 28px;
	}

	&__card {
		border: 1px solid #E7B656;
		padding: 66px 40px;
		backdrop-filter: blur(10px);
		background-color: transparent;
		transition: background-color .3s;

		&:hover {
			background-color: #071723;
		}

		&--large {
			position: relative;
			overflow: hidden;
			width: 68%;
			padding-top: 313px;
			clip-path: polygon(0 1%, 100% 49.6%, 100% 100%, 0 100%);
			
			&::before {
				position: absolute;
				display: block;
				content: "";
				z-index: 2;
				top: 260px;
				left: -50px;
				width: 200%;
				height: 1px;
				background-color: #E7B656;
				transform: rotate(20deg);
			}
		}

		&--small {
			width: 32%;
		}
	}
}

@keyframes gradientAnimation {
	0% {
		background-size: 100% 100%;
	}
	50% {
		background-size: 200% 200%;
	}
	100% {
		background-size: 100% 100%;
	}
}