.services {
  padding: 100px 0;
}
.services__title {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 50px;
  text-align: center;
  font-weight: 400;
  font-size: 325px;
  line-height: 292px;
  letter-spacing: -2px;
  background: linear-gradient(to bottom, #E7B656, #44371c);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 100% 200%;
  animation: gradientAnimation 3s ease infinite;
}
.services__subtitle {
  text-align: center;
  font-size: 40px;
  line-height: 45px;
  font-weight: 500;
}
.services__list {
  display: flex;
  gap: 20px;
  padding: 50px 0;
}
.services__card {
  position: relative;
  overflow: hidden;
  padding: 180px 50px 50px 50px;
  display: flex;
  flex-shrink: 0;
  flex-direction: column;
  height: 600px;
  width: 745px;
  border-radius: 0px 15px 15px 15px;
}
.services__card.is-active .services__card-name {
  color: #E7B656;
}
.services__card.is-active .services__overlay {
  opacity: 1;
}
.services__card--small {
  width: 360px;
  border-radius: 15px;
}
.services__card-bg {
  position: absolute;
  inset: 0;
  z-index: -2;
}
.services__card-bg img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.services__overlay {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: rgba(51, 51, 51, 0.8);
  inset: 0;
  opacity: 0;
  transition: opacity 0.3s;
}
.services__card-name {
  position: relative;
  font-size: 64px;
  line-height: 72px;
  font-weight: 400;
  color: #ffffff;
  padding-bottom: 20px;
}
.services__card-name::before {
  position: absolute;
  content: "";
  display: block;
  left: -50px;
  bottom: 0;
  width: 300px;
  height: 2px;
  background-color: #E7B656;
}
.services__card-description {
  font-size: 24px;
  line-height: 32px;
  color: #ffffff;
}

@keyframes gradientAnimation {
  0% {
    background-size: 100% 100%;
  }
  50% {
    background-size: 200% 200%;
  }
  100% {
    background-size: 100% 100%;
  }
}